@import url(https://fonts.googleapis.com/css2?family=Potta+One&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
.corpo-aplicacao {
    width: 100%;
    background: var(--color-secundary-gradient);
    padding-top: 110px;
    padding-bottom: 60px;
}

@media only screen and (min-width: 600px) {

    .corpo-aplicacao {
        padding-top: 80px;
        padding-bottom: 0;
    }
}
nav {
    z-index:2;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    max-height: 60px;
    box-shadow: 0px -3px 3px black;
    background: var(--color-primary);
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
}

nav a {
    text-decoration: none;
    text-align: center;
    width: 25%;
    height: 100%;
    font-size: var(--font-n);
    font-weight: 700;
    color: var(--color-text);
    padding: 10px;
}

nav a:hover {
    background: var(--color-secundary);
    box-shadow: 0px 0px 12px var(--color-secundary);
    color: var(--color-primary);
}


@media only screen and (min-width: 600px) {
    nav {
        position: relative;
        width: 50%;
        height: 100%;
        max-height: 100px;
        background: var(--color-primary);
        display: flex;
        justify-content: space-between;
        align-items: center;
        box-shadow: unset;
    }
}


header {
    z-index: 2;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 20%;
    max-height: 110px;
    background: var(--color-primary);
    box-shadow: 0px 3px 5px black;
    display: flex;
    justify-content: center;
    align-items: center;
}

#header-logo {
    width: 20%;
    height: 60%;
    background: var(--color-background);
    padding: 15px;
    border-radius: 50%;
    box-shadow: 0px 0px 5px black;
}

@media only screen and (min-width: 600px) {
    header {
        display: flex;
        justify-content: space-between;
        width: 100%;
        max-height: 80px;
        background: var(--color-primary);
        box-shadow: 0px 3px 5px black;

    }

    #header-logo {
        width: 10%;
        max-width: 80px;
        height: 60%;
        margin-left: 15px;
    }
}

.view-home section {
    width: 90%;
    margin: 25px auto;
    padding: 15px 0;
    border-bottom: 2px solid var(--color-primary);
    
}

.view-home section h1 {
    font-size: var(--font-xl);
    font-family: 'Potta One';
    padding: 0 5px;
    color: var(--color-text);
    text-shadow: 0px 0px 3px black;
}


.destaque {
    font-size: var(--font-xl);
    font-family: 'Potta One';
    padding: 0 5px;
    color: var(--color-primary);
}

.destaque-alt {
    font-size: var(--font-xl);
    font-family: 'Potta One';
    padding: 0 5px;
    color: var(--color-alternate);
}

.subtitle {
    font-size: 32px;
    color: var(--color-title);
    font-weight: 600;
    text-shadow: 0px 0px 1px black;
} 
.view-home ul li {
    
}
.view-home p {
    font-size: var(--font-n);
    padding: 0 5px;
    text-shadow: 0px 0px 1px black;
    text-align: center;
    display: block;
}

.view-home li{
    padding: 10px 5px;
}

.agentes-blocos-area {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

}

.agentes-bloco {
    width: 80%;
    margin: 10% auto;
    padding: 5px;
    max-width: 400px;
    box-shadow: 0px 0px 13px black;
    background: var(--color-background);
    border-radius: 24px;
}

.view-home h3 {
    text-align: center;
    color: var(--color-primary);
    margin-bottom: 10px;
    text-transform: uppercase;
}
.form-button {
    padding: 5px 10px;
    outline: none;
    border-radius: 12px;
    font-size: var(--font-n);
    font-weight: 600;
    color: green;
    text-shadow: 0px 0px 1px black;
    background-color: var(--color-primay);
    border: 1px solid green;
}
.perfil-box {
    width: 90%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 10px auto;
    padding: 8px;
    background-color: var(--color-background);
    border-radius: 24px;
    box-shadow: 0px 0px 3px black;
    cursor: pointer;
    transition: .5s;
}

.perfil-box:hover {
    width: 95%;
    margin: 10px auto;
    padding: 8px;
    background-color: var(--color-secundary);
    border-radius: 18px;
    box-shadow: 0px 0px 3px var(--color-alternate);
}

.perfil-box div {
    width: 50%;
}
.perfil-box div span {
    display: block;
}
.perfil-box-label {
    display: flex;
    width: 100%;
    height: 35px;
    text-align: center;
    justify-content: center;
    align-items: center;
    font-size: var(--font-s);
    font-weight: 600;
}
.perfil-box-label img {
    width: 15%;
    height: 100%;
}

@media only screen and (min-width: 600px) {
    .perfil-box div {
        width: 33%;
    }
}
#group-list-busca {
    width: 100%;
    padding: 30px 0;
    background: var(--color-background);
}
label{
    text-decoration: none;
    text-align: center;
    font-size: var(--font-n);
    font-weight: 700;
    font-family: 'Roboto', sans-serif;
    font-size: var(--font-n);
}
#view-entrar {
    width: 100%;
    padding: 50px 0;
    background: var(--color-background);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
}

.container-form {
    width: 45%;
    margin: 15px auto;
    box-shadow: 0px 0px 13px black;
    border-radius: 24px;
    padding: 12px;

}


@media only screen and (max-width: 600px) {
    #view-entrar div {
        width: 95%;
    }
    
    
}
.material {
    display: flex;
    width: 45%;
    height: 80px;
    border: 2px outset black;
    background: var(--color-primary-gradient);
    border-radius: 24px;
    padding: 12px;
    color: var(--color-background);
    text-shadow: 0px 0px 2px black;
    cursor: pointer;
}
.material:hover {
    box-shadow: 0px 0px 18px var(--color-secundary);
    border-color: var(--color-alternate);
}

.material div:first-child {
    width: 40%;
    height: 100%;
}
.material div:last-child {
    width: 60%;
    height: 100%;
}

.material-icon {
    margin: auto;
    width: 80%;
    height: 95%;
    background: var(--color-background);
    border-radius: 50%;
}

.material-rotulo {
    font-size: var(--font-n);
    font-family: "Potta One", cursive;
    width: 100%;
    height: 50%;
    border-bottom: unset;
}

.material-valor {
    width: 50%;
    height: 50%;
}

.material-unidade {
    width: 50%;
    height: 50%;
}

@media only screen and (max-width: 600px) {
    .material {
        width: 49%;
        height: 90px;
    }
}
.material-card {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 40%;
    height: 400px;
    margin: 15px auto;
    padding: 5px;
    box-shadow: 0px 0px 4px black;
    border-radius: 5px;
    background: var(--color-background-alternate);
}

.material-card p {
    width: 100%;
    height: 10%;
    margin: 0;
    border-bottom: 1px solid #ccc;
    text-align: center;
    font-weight: 600;
    font-size: var(--font-l);
}

.material-group {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 15px;
}


@media only screen and (max-width: 600px) {
    .material-card {
        width: 95%;

    }
    .material-group {
        padding: 0 5px;
    }
}
.user-home {
    display: flex;
    flex-wrap: wrap;
    background: var(--color-background);
}


#container-favoritos {
    width: 100%;
}

#container-filtro {
    width: 50%;
    margin: 15px auto;
}

@media only screen and (max-width: 600px) {
    #container-filtro {
        width: 90%;
    }
    
}
.user-perf {
    width: 100%;
    background: var(--color-background);
    padding: 5px 0;
}

.user-perf section {
    width: 90%;
    margin: 25px auto;
}

/* PRIMEIRA SECTION */
.user-perf section:first-child {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}
.info-g {
    width: 60%;
    display: flex;
    flex-wrap: wrap;
}
.profile-icon {
    width: 80%;
    height: 80%;
    border-radius: 50%;
    background: #ccc;
    padding: 5px;
    box-shadow: 0px 0px 3px black;
}
.info-g div:first-child {
    width: 30%;
}
.info-g div:last-child {
    width: 70%;
}
.info-g div:last-child span {
    display: block;
    font-size: var(--font-n);
    font-weight: bolder;
    text-shadow: 0px 0px 1px black;
    padding: 5px;
}

.info-c {
    width: 30%;
    background: var(--color-primary);
    text-shadow: 0px 0px 3px black;
    border-radius: 5px;
    box-shadow: 0px 0px 5px black;
    padding: 8px;
}

.info-c span {
    display: block;
    color: var(--color-text); 
}
.info-c span:first-child {
    border-bottom: 1px solid #ccc;
    margin-bottom: 5px;
    text-align: center;
    font-size: var(--font-l);
    font-weight: bolder;
}

@media only screen and (max-width: 600px) {

    .info-g, .info-c {
        width: 90%;
        margin: 5px auto;
    }

    .info-g div:first-child {
        width: 35%;
        
    }
    .info-g div:last-child {
        width: 65%;
    }
}

/* SEGUNDA SECTION */
.user-perf section:nth-child(2) div:first-child {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-bottom: 25px;
}
.user-perf section:nth-child(2) div:first-child h2 {
    width: 100%;
}
.user-perf section:nth-child(2) div:first-child span {
    padding: 5px 15px;
    font-size: var(--font-l);
    font-weight: bolder;
    box-shadow: 0px 0px 3px #ccc;
    border-radius: 5px;
}

/* TERCEIRA SECTION */
.user-perf section:last-child {
    display: flex;
    flex-wrap: wrap;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    /* 
    font-family: 'Potta One', cursive;
    */
    font-family: 'Roboto', sans-serif;

    --color-primary:  rgba(44,110,73,1);
    --color-primary-gradient: linear-gradient(158deg, rgba(238,239,32,1) 0%, rgba(44,110,73,1) 34%);
    --color-secundary: #95d5b2;
    --color-secundary-gradient: linear-gradient(158deg, rgba(238,239,32,1) 0%, #95d5b2 34%) #95d5b2;
    --color-background: #f8f9fa;
    --color-background-alternate: ;
    --color-alternate:  rgb(238,239,32);
    --color-line: ;
    --color-text: #ccc;
    --color-title: #55a630;


    --font-xs: 10px;
    --font-s: 14px;
    --font-n: 18px;
    --font-l: 22px;
    --font-xl: 28px;

}

.desk-view {
    display: none;
}
.d-none {
    display: none;
}

@media only screen and (min-width: 600px) {
    .desk-view {
        display: block;
    }
}

.main-logged {
    width: 100%;
    height: 100vh;
    padding: 30px 0;
    background: var(--color-background);
}
